<template>
  <div>
    <b-table-simple class="table table-dark">
        <b-thead>
          <b-th>
            <b-icon
              icon="info-circle"
              scale="1"
              variant="info"
              title="Use this toggle to pause a DAG.
                  The scheduler won't schedule new tasks instances
                  for a paused DAG. Tasks already running at pause time won't be affected."
            ></b-icon>
          </b-th>
          <b-th>Dag_Id</b-th>
          <b-th>
            Last Run
            <b-icon
              icon="info-circle"
              scale="1"
              variant="info"
              title="Execution Date/Time of Highest Dag Run."
            ></b-icon>
          </b-th>
          <b-th>DAG Runs</b-th>
        </b-thead>
        <tbody>
          <b-tr v-for="dag in dags" :key="dag.dag_id">
            <b-td>
              <b-form-checkbox
                switch
                :checked="dag.is_active"
              ></b-form-checkbox>
            </b-td>
            <b-td>
              <a v-bind:href="`https://e230-pc07/flow/admin/airflow/graph?dag_id=${ dag.dag_id }&execution_date=${ new Date(dag.last_scheduler_run).toISOString() }`">
              {{ dag.dag_id }}
              </a>
            </b-td>
            <b-td
            v-if="getLastDagRunOfDag(dag.dag_id)"
            >
              {{ new Date(getLastDagRunOfDag(dag.dag_id).execution_time).toISOString() }}
              <b-button
              v-if="getLastDagRunOfDag(dag.dag_id).state == 'success'"
              variant="outline-success"
              style="margin-left: 3px"
              >
              {{ getLastDagRunOfDag(dag.dag_id).state }}
              </b-button>
              <a
                v-if="getLastDagRunOfDag(dag.dag_id).state == 'failed'"
                v-bind:href="`https://e230-pc07/flow/admin/airflow/log?task_id=${ getLastDagRunOfDag(dag.dag_id).failed_task_id }&dag_id=${ dag.dag_id }&execution_date=${ encodeURI(new Date(getLastDagRunOfDag(dag.dag_id).execution_time).toISOString()) }&format=json`"
              >
                <b-button
                variant="outline-danger"
                style="margin-left: 3px"
                >
                {{ getLastDagRunOfDag(dag.dag_id).state }}
                </b-button>
              </a>
            </b-td>
            <b-td
            v-else
            >
            <span></span>
            </b-td>
            <b-td>
              <b-button
                disabled
                pill
                variant="outline-success"
                style="margin-right: 3px"
              >
                {{ countSuccesfulDagRuns(dag.dag_id) }}
              </b-button>
              <b-button
                disabled
                pill
                variant="outline-danger"
                style="margin-left: 3px"
              >
                {{ countFailedDagRuns(dag.dag_id) }}
              </b-button>
            </b-td>
          </b-tr>
        </tbody>
      </b-table-simple>
    <!--<serviceDagsTable :serviceDags="serviceDags" :dagRuns="dagRuns"></serviceDagsTable>-->
  </div>
</template>

<script>
// import ServiceDagsTable from './ServiceDagsTable.vue';

export default {
  components: {
    // serviceDagsTable: ServiceDagsTable,
  },
  mounted() {
    /* fetch('http://e230-pc07:8081/flow/kaapana/api/getdags')// + <domain> at the beginning
      .then((response) => response.json())
      .then((data) => {
        this.dags = data;
        console.log(data);
      });
   fetch('https://e230-pc07/flow/kaapana/api/getdags')// + <domain> at the beginning
      .then((response) => response.json())
      .then((data) => {
        this.dags = data;
        console.log(data);
      }); */
    this.fetchDags();
    this.fetchDagRuns();
    // this.filterAfterServiceDags();
  },
  data() {
    return {
      serviceDags: [],
      nonServiceDags: [],
      dags: [],
      dagRuns: [],
      // logs: https://e230-pc07/flow/admin/airflow/get_logs_with_metadata?dag_id=dcm2nrrd&task_id=dcm-converter&execution_date=2020-10-05T12%3A13%3A00.938440%2B00%3A00&try_number=1&metadata=null
      // graph: https://e230-pc07/flow/admin/airflow/graph?dag_id=dcm2nrrd&execution_date=2020-10-05+12%3A13%3A00.938440%2B00%3A00
      // restartDagRun: http://localhost:8080/api/experimental/dags/<DAG_ID>/dag_runs
    };
  },
  methods: {
    async fetchDags() {
      try {
        const res = await fetch('getdags.json');
        const val = await res.json();
        this.dags = val;
        this.filterAfterServiceDags();
      } catch (e) {
        console.log(e);
      }
    },
    async fetchDagRuns() {
      try {
        const res = await fetch('getdagruns.json');
        const val = await res.json();
        this.dagRuns = val;
      } catch (e) {
        console.log(e);
      }
    },
    countSuccesfulDagRuns(dagId) {
      let equalDagRuns = [];
      equalDagRuns = this.dagRuns.filter(
        (dagRun) => dagRun.dag_id === dagId && dagRun.state === 'success',
      );
      return equalDagRuns.length;
    },
    countFailedDagRuns(dagId) {
      let equalDagRuns = [];
      equalDagRuns = this.dagRuns.filter(
        (dagRun) => dagRun.dag_id === dagId && dagRun.state === 'failed',
      );
      return equalDagRuns.length;
    },
    filterAfterServiceDags() {
      /* eslint no-plusplus: ["error", { "allowForLoopAfterthoughts": true }] */
      /* Simple iteration with forEach doesn't work,
         because function isn't able to iterate over directory. */
      const dagKeys = Object.keys(this.dags);
      let i;
      for (i = 0; i < dagKeys.length; i++) {
        if (dagKeys[i].substring(0, 7) === 'service') {
          this.serviceDags.push(this.dags[dagKeys[i]]);
        } else {
          this.nonServiceDags.push(this.dags[[dagKeys[i]]]);
        }
      }
    },
    getLastDagRunOfDag(dagId) {
      let lastDagRuns = [];
      let lastDagRun;
      console.log(dagId);
      lastDagRuns = this.dagRuns.filter(
        (dagRun) => dagRun.dag_id === dagId,
      );
      if (!lastDagRuns) {
        lastDagRun = 'No match';
        console.log(lastDagRun);
      } else {
        lastDagRun = lastDagRuns[lastDagRuns.length - 1];
        console.log(lastDagRun);
      }
      return lastDagRun;
    },
  },
};
</script>

